html,
body {
  min-height: 100vh; }

body {
  &.frozen-body {
    overflow: hidden; }

  &.toolbar-horizontal {
    .menu-administration-user-content {
      max-height: calc(100vh - 79px); } } }
.layout-container {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100vh;

  // Main content.
  > main {
    flex-grow: 1;
    //padding: 3rem $container-padding
    padding: 0;
    width: 100%; }

  .layout-wrapper {
    //+container
    display: flex;
    flex-direction: column;
    @include breakpoint($large) {
      flex-direction: row;
      flex-wrap: wrap; }

    > *:not(:last-child) {
      @include breakpoint(0 $bellow-large) {
        margin-bottom: 1rem; } }

    .layout-content {
      width: 100%;
      @include breakpoint($large) {
        flex-grow: 1;
        width: 50%; } }

    aside {
      width: 100%;
      @include breakpoint($large) {
        width: 23.5%; }

      &.layout-sidebar-first {
        @include breakpoint($large) {
          margin-right: 1.5%;
          order: -1; } }

      &.layout-sidebar-second {
        @include breakpoint($large) {
          margin-left: 1.5%; } } } } }

.ie {
  .layout-container {
    display: block; } }
